import React, {useState} from 'react'

const Create = ({addData}) => {

   const [title, setTitle] = useState('');
   const [content, setContent] = useState('');

   return (
      <form className="form" onSubmit={d => addData(d, {title, content})}>
         <div>
            <label>Title: </label><br />
            <input type="text" name="title" placeholder="Enter note title" value={title} onChange={d => {setTitle(d.target.value)}}></input>
         </div>
         <div>
            <label>Content: </label><br />
            <textarea name="content" placeholder="Enter note content" onChange={d => {setContent(d.target.value)}} value={content}></textarea>
         </div>
         <div className="btn-container">
            <button className="mn-btn" type="submit">+ Note</button>
         </div>
      </form>
   )
}

export default Create
