import React, { useEffect, useState } from "react";
import "./App.css";
import Form from "./Form";
import List from "./List";

const App = () => {
  const [recipes, setRecipes] = useState([
    { title: "" },
    { title: "" }
  ]);
  
  const [sChange, setChange] = useState(0);

  const myUrl = "http://192.168.43.190:3001/notes";

  useEffect(() => {
    getRecipe();
  }, [sChange]);

  const getRecipe = async () => {
    const res = await fetch(myUrl);
    const data = await res.json();
    console.log(data);
    setRecipes(data);
  };

  const addData = async (d, body) => {
    d.preventDefault();
    const res = await fetch(myUrl, {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(body)
    });
    const ret = await res.json();
    console.log(ret);
    setChange(sChange+1);
  }

  const noteAction = async (noteId, method, data) => {
    console.log(data);
    const res = await fetch(myUrl + "/" + noteId, {
      method: method,
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(data)
    });
    const ret = await res.json();
    console.log(ret);
    setChange(sChange+1);
  }


  return (
    <div className="App">
      <h1>React Node Note App</h1>
      <div className="seprator"></div>
      <Form addData={addData}></Form>
      <div className="seprator"></div>
      <List allNotes={recipes} noteAction={noteAction}></List>
    </div>
  );
};

export default App;
