import React from 'react'

const List = ({allNotes, noteAction}) => {

   
   if(!allNotes || !allNotes[0].content){
      return (
         <div className="err">
            <p>Something went wrong...</p>
         </div>
      )
   }

   // Changing order

   let descNotes = [];
   let i,j;

   for(i = 0, j = allNotes.length - 1; i < allNotes.length; i++, j--){
      descNotes[i] = allNotes[j];
   }

   console.log(descNotes);

   const itemSubmit = (noteId, e) => {
      const title = e.target.children[0].innerText;
      const content = e.target.children[1].innerText;

      noteAction(noteId, "put", {title, content});
   }

   return (
      <div className="noteList">
         {
            descNotes.map(note => {
               return (
               <form className="noteMain" key={note._id} onSubmit={e => itemSubmit(note._id, e)}>
                  <h3 contentEditable="true">{note.title}</h3>
                  <p contentEditable="true">{note.content}</p>
                  <span>
                     <button type="submit" className="mn-btn">Update</button>
                     <a href="!#" onClick={e => noteAction(note._id, "delete", {})}>Delete</a>
                  </span>
               </form>
            )}
            )
         }
      </div>
   )
}

export default List
